                    name:'', gender:'', area:'', phone:'', mail:'', center:'', dept:'',
                    role:'', leader:'', on_job:''
                },
                //编辑框
                editFormVisible:false,
                //MOCK用户信息
                userInfo: JSON.parse(sessionStorage.getItem('userInfo'))
            }
        },
        mounted() {
            this.getUserInfo();
        },
        methods:{
            pageList(){
                // 发请求拿到数据并暂存全部数据,方便之后操作
                this.data = listJson.tableData_user;
                this.getList();
            },
            // 处理数据
            getList() {
                // es6过滤得到满足搜索条件的展示数据list
                let tableData_user = this.data.filter((item, index) =>
                    item.name.includes(this.searchName) && item.gender.includes(this.searchGender) &&
                    item.area.includes(this.searchArea) && item.center.includes(this.searchCenter) &&
                    item.phone.includes(this.searchPhone) && item.mail.includes(this.searchMail) &&
                    item.dept.includes(this.searchDept)  && item.role.includes(this.searchRole) &&
                    item.group.includes(this.searchGroup)  && item.s_leader.includes(this.searchLeader)
                    && item.on_job.includes(this.searchOnJob)
                );

                //过滤分页
                this.tableData_user = tableData_user.filter((item, index) =>
                    index < this.currentPage * this.PageSize && index >= this.PageSize * (this.currentPage - 1)
                );
                //分页的总数据
                this.total = tableData_user.length;
            },
            indexMethod(index){
                return (index+1)+(this.currentPage-1)*this.PageSize;
            },
            // getData() {
            // console.log('生命周期')
            // this.axios.get('../../static/database.json').then(response => {
            // console.log(response.data);
            // this.tableData_user=response.data
            // }, response => {
            // console.log("error");
            // });
            // },
            // 分页
            // 每页显示的条数
            handleSizeChange(val) {
                // 改变每页显示的条数
                this.PageSize=val;
                this.getList();
            },
            // 显示第几页
            handleCurrentChange(val) {
                // 改变默认的页数
                this.currentPage=val;
                this.getList();
            },
            search(){
                this.currentPage=1;// 注意：在改变每页显示的条数时，要将页码显示到第一页
                this.getList();
            },
            // 点击编辑按钮
            userEdit(index,row){
                this.editFormVisible = true; //编辑信息模态框显示
                this.editsForm = Object.assign({}, row);  //获得所有数据显示在编辑信息模态框里面
            },
