<template>
    <div id="register">
        <el-row style="height: 80px;background-color: rgb(255,255,255,.9);">
<!--            <img src="../../assets/logo.png" style="position: absolute;bottom: 10px;left: 100px;">-->
            <span style="position: absolute;bottom: 10px;left: 450px;font-size: 24px;">园区共享服务大数据支持平台</span>
        </el-row>
        <div class="login-container">
            <el-form :model="registerForm" :rules="registerRules" label-position="left" ref="registerForm" label-width="100px" class="login-form" style="width: 800px;margin:60px auto;padding: 30px 50px 10px 20px">
                <el-row>
                    <el-col :span="12">
                        <el-form-item prop="name" label="姓名">
                            <el-input v-model="registerForm.name"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item style="padding-left: 20px;" prop="gender" label="性别">
                            <el-select v-model="registerForm.gender">
                                <el-option label="男" value="1"></el-option>
                                <el-option label="女" value="2"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item prop="phone" label="手机号码">
                            <el-input v-model="registerForm.phone"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item style="padding-left: 20px;" prop="email" label="电子邮箱">
                            <el-input v-model="registerForm.email"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item prop="company" label="所属单位">
                            <el-select v-model="registerForm.company">
                                <el-option label="物业" value="1"></el-option>
                                <el-option label="软件开发中心" value="2"></el-option>
                                <el-option label="银行审批中心" value="3"></el-option>
                                <el-option label="后台服务中心" value="4"></el-option>
                                <el-option label="国际业务中心" value="5"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item style="padding-left: 20px;" prop="dep" label="所属部门">
                            <el-select v-model="registerForm.dep">
                                <el-option label="研发部" value="1"></el-option>
                                <el-option label="产品部" value="2"></el-option>
                                <el-option label="人事部" value="3"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item prop="grp" label="所属小组">
                            <el-select v-model="registerForm.grp">
                                <el-option label="组1" value="grp1"></el-option>
                                <el-option label="组2" value="grp2"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item style="padding-left: 20px;" prop="area" label="办公区">
                            <el-input v-model="registerForm.area"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="密码" prop="pass0">
                            <el-input type="password" v-model="registerForm.pass0" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item prop="role" label="角色" style="padding-left: 20px;">
                            <el-select v-model="registerForm.role">
                                <el-option label="普通用户" value="7"></el-option>
                                <el-option label="小组长" value="8"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="确认密码" prop="pass1">
                            <el-input type="password" v-model="registerForm.pass1" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item style="display: inline-block" prop="answer1" label-width="150px" label="问题1: 您的生日？">
                            <el-input v-model="registerForm.answer1" style="display: inline-block"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item style="padding-left: 20px;" prop="answer2" label="问题2: 毕业于哪个初中？" label-width="190px">
                            <el-input v-model="registerForm.answer2"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="问题3: 您最喜欢的电影？" prop="answer3" label-width="190px">
                            <el-input v-model="registerForm.answer3"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-form-item>
                        <el-button style="float: right;" type="primary" @click="submitForm('registerForm')">注册</el-button>
                    </el-form-item>
                </el-row>
            </el-form>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Register",
        data() {
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请再次输入密码'));
                } else if (value !== this.registerForm.pass0) {
                    callback(new Error('两次输入密码不一致!'));
                } else {
                    callback();
                }
            };
            return {
                registerForm: {
                    name:'',
                    gender:'',
                    phone:'',
                    email:'',
                    company:'',
                    dep:'',
                    grp:'',
                    area:'',
                    role:'',
                    pass0:'',
                    pass1:'',
                    answer1:'',
                    answer2:'',
                    answer3:'',
                },
                registerRules: {
                    name:[
                        { required: true, message: '请输入姓名', trigger: 'blur' }
                    ],
                    gender:[
                        { required: false,}
                    ],
                    phone:[
                        { required: true, message: '请输入手机号码', trigger: 'blur' }
                    ],
                    email: [
                        { required: true, message: '请输入邮箱地址', trigger: 'blur' },
                        { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
                    ],
                    company:[
                        { required: true, message: '请选择单位', trigger: 'blur',}
                    ],
                    dep:[
                        { required: true, message: '请选择部门', trigger: 'blur',}
                    ],
                    grp:[
                        { required: true, message: '请选择小组', trigger: 'blur',}
                    ],
                    area:[
                        { required: false, }
                    ],
                    role:[
                        { required: true, message: '请选择角色', trigger: 'blur'}
                    ],
                    pass0:[
                        { required: true, message: '请输入密码', trigger: 'blur' }
                    ],
                    pass1:[
                        { required: true, validator: validatePass, trigger: 'blur' }
                    ],
                    answer1:[
                        { required: true, message: '请输入密保答案', trigger: 'blur' }
                    ],
                    answer2:[
                        { required: true, message: '请输入密保答案', trigger: 'blur' }
                    ],
                    answer3:[
                        { required: true, message: '请输入密保答案', trigger: 'blur' }
                    ],
                }
            };
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    var user= {
                        email: this.registerForm.email,
                        password: this.registerForm.pass0,
                        userName: this.registerForm.name,
                        gender: Number(this.registerForm.gender),
                        roomNum: this.registerForm.area,
                        phoneNum: this.registerForm.phone,
                        groupName: this.registerForm.grp,
                        depName: this.registerForm.dep,
                        companyName: this.registerForm.company,
                        roleId: Number(this.registerForm.role),
                        answer_1: this.registerForm.answer1,
                        answer_2: this.registerForm.answer2,
                        answer_3: this.registerForm.answer3,
                    };
                    console.log(user)
                    if (valid) {
                        this.$axios.post('/reg', {
                                data: JSON.stringify(user)
                            }
                        )
                        .then((response) => {
                            if (response.status === 200) {
                                this.$message({
                                    message: '注册成功',
                                    type: 'success'
                                })
                                this.$router.push('/login')
                            }
                        })
                        .catch(function (error) {
                            console.log(error)
                        })
                    } else {
                        console.log('error submit!!')
                        return false
                    }
                });
            },
        }
    }
</script>

<style>
    #register{
        width: 100vw;
        height: 100vh;
        background-image: url("../../assets/bg.jpg");
    }
</style>