// 引入mockjs
const Mock = require('mockjs')
// 获取 mock.Random 对象
const Random = Mock.Random

// 使用mockjs模拟数据
let emailList = []
for (let i = 0; i < 100; i++) {
    let newObject = {
        date: Random.datetime(), // 随机生成日期时间
        theme: Random.cname()// 随机生成中文名字
    }
    emailList.push(newObject)
}
Mock.mock('/api/emailList', () => {
    return {
        code: '0',
        data: emailList
    }
})
