<template>
    <div id="check">
        <div style="padding-left: 10px; border-left: 5px solid rgb(91,155,213);">？？？？\？？？\签入签出</div>
        <el-row style="margin:20px 40px 0 40px;">
            <el-row>
                <p class="date">当前时间：{{nowTimes.yyyy}}-{{nowTimes.mm}}-{{nowTimes.dd}} {{nowTimes.hou}}:{{nowTimes.min}}:{{nowTimes.sec}}</p>
            </el-row>
            <p>欢迎您，xxx</p>
            <el-col class="quantity" :span="4">
                <el-button circle type="primary" @click="checkIn" style="text-align:center;width: 100px; height: 100px;font-size: 30px;line-height: 17px;">签入</el-button>
            </el-col>
            <el-col class="quantity" :span="4">
                <el-button circle type="success" @click="checkOut" style="text-align:center;width: 100px; height: 100px;font-size: 30px;line-height: 17px;">签出</el-button>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: "CheckInOrOut",
        data(){
            return{
                nowTimes:{
                    yyyy:"0000",
                    mm:0,
                    dd:"00",
                    hou:"00",
                    min:"00",
                    sec:"00"
                },
                //MOCK用户信息
                userInfo: JSON.parse(sessionStorage.getItem('userInfo'))
            }
        },
        mounted(){
            this.getUserInfo();
            setInterval( ()=>{
                this.setNowTimes();
            },1000)
        },
        methods:{
            //时间
            setNowTimes(){
                let myDate = new Date();
                this.nowTimes.yyyy = myDate.getFullYear();
                let month = myDate.getMonth()+1;
                this.nowTimes.mm = String(month<10?'0'+ month: month);
                this.nowTimes.dd = String(myDate.getDate()<10?'0'+ myDate.getDate(): myDate.getDate()) ;
                this.nowTimes.hou = String(myDate.getHours()<10?"0"+myDate.getHours():myDate.getHours());
                this.nowTimes.min = String(myDate.getMinutes()<10?'0'+myDate.getMinutes():myDate.getMinutes());
                this.nowTimes.sec = String(myDate.getSeconds()<10?'0'+myDate.getSeconds():myDate.getSeconds());
            },
            //按钮
            checkIn() {
                this.$alert('签入成功！', '提示', {
                    confirmButtonText: '确定',
                });
            },
            checkOut() {
                this.$alert('签出成功！', '提示', {
                    confirmButtonText: '确定',
                })
            },
        }
    }
</script>

