<template>
    <div class="entity_detail">
        <div style="padding-left: 10px; border-left: 5px solid rgb(91,155,213);">
            <router-link to="/home" style="color: #606266;">首页</router-link>
            \待办信息
        </div>
        <!--待办信息详情界面-->
        <el-row id="entity_info">
            <p class="entity_p">主题:停车场申请</p>
            <p class="entity_p">原因:</p>
            <p class="entity_p">占用时间:</p>
<!--            <p class="todo_p">长期占用</p>&lt;!&ndash;公共办公区长期占用&ndash;&gt;-->
            <!--停车场才有申请数量-->
            <p class="entity_p">申请数量:</p>
            <p class="entity_p">备注:</p>
            <p class="entity_p" style="text-align: right;">申请时间:</p>
            <p class="entity_p"style="text-align: right;">申请人:</p>
        </el-row>
        <el-row id="entity_info_button">
            <el-button type="primary" class="application_pass" @click="pass">通过</el-button>
            <el-button type="danger" class="application_reject" @click="reject">拒绝</el-button>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: "Entity_detail",
        methods: {
            pass() {
                this.$confirm('同意该申请?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '申请通过!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消操作'
                    });
                });
            },
            reject() {
                this.$confirm('拒绝该申请？', '拒绝', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'

                })
                    .then(() => {
                    this.$message({
                        type: 'success',
                        message: '已拒绝申请！'
                    });
                })
                    .catch(() => {
                    this.$message({
                        type: 'info',
                        message: '取消输入'
                    });
                });
            },

        }
    }
</script>

<style>
    #entity_info{
        width: 60%; border-top: 2px solid rgb(91,155,213);margin-top:20px;margin-left: 40px;background-color: white;padding: 0 20px 0 20px;line-height: 30px
    }

    #entity_info .entity_p{
        border-bottom: 1px solid #eeeeee;
    }
    #entity_info_button{
        margin-top:20px;margin-left: 40px;
    }
</style>