<template>
    <div id="sign">
        <div style="padding-left: 10px; border-left: 5px solid #5B9BD5;">公共资源\???\业主登录</div>
        <el-row style="margin:20px 40px 0 40px;">
            <!--当前时间-->
            <el-row>
                <p class="date">当前时间：{{nowTimes.yyyy}}-{{nowTimes.mm}}-{{nowTimes.dd}} {{nowTimes.hou}}:{{nowTimes.min}}:{{nowTimes.sec}}</p>
            </el-row>
            <el-form ref="form" :rules="signRules" :model="signForm" :label-position="labelPosition" label-width="80px" class="login-form" style="margin: 20px 0 0 0;">
                <el-form-item label="用户名" prop="username">
                    <el-input v-model="signForm.username"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input v-model="signForm.password"></el-input>
                </el-form-item>

                <el-button type="primary" @click="submitForm('signForm')" style="margin-left:0;width: 350px">登录</el-button>
            </el-form>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: "Sign",
        data() {
            return {
                //时间
                nowTimes:{
                    yyyy:"0000",
                    mm:0,
                    dd:"00",
                    hou:"00",
                    min:"00",
                    sec:"00"
                },
                //登录框
                signForm: {
                    username: "",
                    password: ""
                },
                labelPosition:'left',
                signRules:{
                    username:[
                        {required: true, message: "用户名不能为空", trigger: 'blur'},
                        {min: 3, max: 10, message: "用户名3-5位", trigger: 'blur'}
                    ],
                    password: [
                        {required: true, message: "密码不能为空", trigger: 'blur'},
                        {min: 3, max: 10, message: "密码3-5位", trigger: 'blur'}
                    ]
                },
                //MOCK用户信息
                userInfo: JSON.parse(sessionStorage.getItem('userInfo'))
            };
        },
        mounted(){
            this.getUserInfo();
            //当前时间
            setInterval( ()=>{
                this.setNowTimes();
            },1000)
        },
        methods: {
            //时间
            setNowTimes(){
                let myDate = new Date();
                let month = myDate.getMonth()+1;
                this.nowTimes.yyyy = myDate.getFullYear();
                this.nowTimes.mm = String(month<10?'0'+ month: month);
                this.nowTimes.dd = String(myDate.getDate()<10?'0'+ myDate.getDate(): myDate.getDate()) ;
                this.nowTimes.hou = String(myDate.getHours()<10?"0"+myDate.getHours():myDate.getHours());
                this.nowTimes.min = String(myDate.getMinutes()<10?'0'+myDate.getMinutes():myDate.getMinutes());
                this.nowTimes.sec = String(myDate.getSeconds()<10?'0'+myDate.getSeconds():myDate.getSeconds());
            },
            submitForm(formName) {
                this.$router.push("/checkinorout");
                this.$refs[formName].validate(valid => {
                    // console.log(valid) 验证通过为true，有一个不通过就是false
                    if (valid) {
                        // 通过的逻辑
                        this.$router.replace("/checkinorout");
                    } else {
                        console.log('验证失败');
                        return false;
                    }
                });
            },
        },
    }
</script>

