<template>
    <div id="record_application">
        <div style="padding-left: 10px; border-left: 5px solid rgb(91,155,213);">综合服务\人员出入记录
        </div>

        <el-row style="margin-top:20px;margin-left: 40px;width: 30%; border-top: 2px solid rgb(91,155,213);background-color: white;padding: 20px 20px 0 0;line-height: 30px
">
            <el-form :model="recordForm" status-icon :rules="rules" ref="recordForm" label-width="130px" class="demo-recordForm">
                <el-form-item label="查询人员姓名:" prop="name">
                    <el-input v-model="recordForm.pass" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="起始时间：" prop="startTime">
                    <template>
                        <el-date-picker
                                v-model="value1"
                                type="datetime"
                                placeholder="选择日期时间">
                        </el-date-picker>
                    </template>
                </el-form-item>
                <el-form-item label="截止时间:" prop="endTime">
                    <template>
                        <el-date-picker
                                v-model="value1"
                                type="datetime"
                                placeholder="选择日期时间">
                        </el-date-picker>
                    </template>
                </el-form-item>
                <el-form-item label="查询条件" prop="in_out">
                    <el-select v-model="searchInOut" style="width: 120px;">
                        <el-option label="入园" value="入园"></el-option>
                        <el-option label="出园" value="出园"></el-option>
                        <el-option label="不限" value=""></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('recordForm')" style="float: right;">确定</el-button>
                </el-form-item>
            </el-form>

        </el-row>
    </div>
</template>

<script>
    export default {
        name: "Record_application",
        data() {
            return {
                recordForm: {
                    name: '',
                    startTime: '',
                    endTime: '',
                    in_out:''
                },
                rules: {
                    name:[
                        { required: true, trigger: 'blur' }
                    ],
                    in_out:[
                        {required:true, trigger:'blur'}
                    ]
                },
                //MOCK用户信息
                userInfo: JSON.parse(sessionStorage.getItem('userInfo'))
            };
        },
        mounted() {
            this.getUserInfo();
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
        }
    }
</script>

