<template>
    <div id="record">
        <div style="padding-left: 10px; border-left: 5px solid #5B9BD5;">综合服务\人员出入记录</div>
        <el-row style="margin:20px 40px 0 40px;">
            <el-col :span="18">
                <div class="searchWord" style="font-size: 14px;">
                    <div style="display: inline-block;margin-bottom: 10px;">
                        姓名：
                        <el-input v-model="searchName" style="margin-right:20px;width:120px;display: inline-block;"
                                  placeholder="请输入姓名">
                        </el-input>
                    </div>
                    <div style="display: inline-block;margin-bottom: 10px;">
                        入园\出园：
                        <el-select v-model="searchInOut" style="width: 120px;">
                            <el-option label="入园" value="入园"></el-option>
                            <el-option label="出园" value="出园"></el-option>
                            <el-option label="不限" value=""></el-option>
                        </el-select>
                    </div>
                    <div style="display: inline-block;margin-bottom: 10px;margin-left: 20px;">
                        单位：
                        <el-select v-model="searchCenter" style="width: 150px;">
                            <el-option label="软件开发中心" value="软件开发中心"></el-option>
                            <el-option label="银行审批中心" value="银行审批中心"></el-option>
                            <el-option label="后台服务中心" value="后台服务中心"></el-option>
                            <el-option label="国际业务中心" value="国际业务中心"></el-option>
                            <el-option label="不限" value=""></el-option>
                        </el-select>
                    </div>
                    <div style="display: inline-block;margin-bottom: 10px;margin-left: 20px;">
                        部门：
                        <el-select v-model="searchDept" style="width: 150px;">
                            <el-option label="研发部" value="研发部"></el-option>
                            <el-option label="产品部" value="产品部"></el-option>
                            <el-option label="人事部" value="人事部"></el-option>
                            <el-option label="不限" value=""></el-option>
                        </el-select>
                    </div>
                    <div class="block" style="display: inline-block;font-size: 14px;">
                        <span class="date">开始日期：</span>
                        <el-date-picker style="width: 150px;"
                                        v-model="startTime"
                                        type="datetime"
                                        placeholder="选择日期">
                        </el-date-picker>
                    </div>
                    <div class="block" style="display: inline-block;margin-left: 20px;font-size: 14px;">
                        <span class="date">结束日期：</span>
                        <el-date-picker style="width: 150px;"
                                        v-model="endTime"
                                        type="datetime"
                                        placeholder="选择日期">
                        </el-date-picker>
                    </div>
                    <el-button style="margin-left: 20px;" type="primary" @click="search" size="small">搜索</el-button>
                </div>

                <el-table id="table-record" style="margin-top:20px;border-top: 2px solid rgb(91,155,213);"
                          :data="tableData_record"
                          :current-page="currentPage">
                    <el-table-column width="50" prop type="index" :index="indexMethod">
                    </el-table-column>

                    <el-table-column label="时间" prop="date">
                    </el-table-column>
                    <el-table-column label="姓名" prop="name">
                    </el-table-column>
                    <el-table-column label="入园\出园" prop="in_out">
                    </el-table-column>
                    <el-table-column label="单位" prop="center">
                    </el-table-column>
                    <el-table-column label="部门" prop="dept">
                    </el-table-column>
                </el-table>
                <el-row class="pagination" style="margin-top: 10px; float: right;">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-size="PageSize"
                            layout="total, prev, pager, next, jumper"
                            :total="total">
                    </el-pagination>
                </el-row>
            </el-col>
            <el-col class="quantity" :span="4" style="margin-left: 50px;" v-if="this.userInfo.roleId==6">
                <el-button circle type="success" @click="goToLink" style="text-align:center;width: 100px; height: 100px;font-size: 30px;line-height: 17px;">签入<br></br>签出</el-button>
            </el-col>
            <el-col v-if="this.userInfo.roleId==10">
                <p>您可以查看xx中心的人员出入记录</p>
                <el-button type="primary" @click="goToRecordApplication">我要申请</el-button>
            </el-col>
            <el-col v-if="this.userInfo.roleId==9">
                <p>您可以查看xx中心xx部门的人员出入记录</p>
                <el-button type="primary" @click="goToRecordApplication">我要申请</el-button>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    var listJson = {
        tableData_record:[{
            date: '2020-07-22',
            name: '小明',
            in_out: '入园',
            center: '软件开发中心',
            dept: '产品部'
        },{
            date: '2020-07-22',
            name: '小亮',
            in_out: '出园',
            center: '软件开发中心',
            dept: '研发部'
        },{
            date: '2020-07-22',
            name: '小红',
            in_out: '入园',
            center: '远程银行中心',
            dept: 'xx部'
        },],
    }
    export default {
        name: "Record",
        data(){
            return{
                //总数据
                tableData_record:[],
                data:[],
                //默认显示第1页
                currentPage:1,
                //默认每页显示条数
                PageSize:6,
                //总条数
                total: null,
                //搜索
                searchName: '',
                searchInOut: '',
                searchCenter: '',
                searchDept:'',
                //时间段
                startTime:'',
                endTime:'',
                //MOCK用户信息
                // userInfo: JSON.parse(sessionStorage.getItem('userInfo'))
                //store里的user信息
                userInfo: JSON.parse(this.$store.state.user)
            }

        },
        methods:{
            pageList(){
                // 发请求拿到数据并暂存全部数据,方便之后操作
                this.data = listJson.tableData_record;
                this.getList();
            },
            // 处理数据
            getList() {
                // es6过滤得到满足搜索条件的展示数据list
                let tableData_record = this.data.filter((item, index) =>
                    item.name.includes(this.searchName) && item.in_out.includes(this.searchInOut) && item.center.includes(this.searchCenter)
                    && item.dept.includes(this.searchDept)
                );

                //过滤分页
                this.tableData_record = tableData_record.filter((item, index) =>
                    index < this.currentPage * this.PageSize && index >= this.PageSize * (this.currentPage - 1)
                );
                //分页的总数据
                this.total = tableData_record.length;
            },
            indexMethod(index){
                return (index+1)+(this.currentPage-1)*this.PageSize;
            },
            // getData() {
            // console.log('生命周期')
            // this.axios.get('../../static/database.json').then(response => {
            // console.log(response.data);
            // this.tableData_record=response.data
            // }, response => {
            // console.log("error");
            // });
            // },
            // 分页
            // 每页显示的条数
            handleSizeChange(val) {
                // 改变每页显示的条数
                this.PageSize=val;
                this.getList();
            },
            // 显示第几页
            handleCurrentChange(val) {
                // 改变默认的页数
                this.currentPage=val;
                this.getList();
            },
            search(){
                this.currentPage=1;// 注意：在改变每页显示的条数时，要将页码显示到第一页
                this.getList();
            },
            //签到
            goToLink(){
                this.$router.replace('/checkinorout')
            },
            //转申请详情
            goToRecordApplication(){
                this.$router.replace('/record_application')
            },
        },mounted() {
            // this.getUserInfo();
        },
        created(){
            // this.getData()
            this.pageList();
        },
    }
</script>

