<script>
    export default {
        name: "Canteen1",
        data(){
            return{
                //是否是部门领导
                isDepartment: true,
                //房间
                roomList:[
                    {room_number: 115, isBusy: false},
                    {room_number: 116, isBusy: true},
                    {room_number: 510, isBusy: false},
                ],
                //房间时段
                timeList:[
                    {room_number: 115},
                    {room_number: 116},
                    {room_number: 510},
                ],
                //时间
                nowTimes:{
                    yyyy:"0000",
                    mm:0,
                    dd:"00",
                    hou:"00",
                    min:"00",
                    sec:"00"
                },
                //弹出申请详情
                dialogFormVisible: false,
                applyForm: {
                    name: '', //主题
                    note: '',//备注
                    reason: '', //申请原因
                },
                rules:{
                    name:[
                        { required: true, message: '请输入主题', trigger: 'blur' },
                    ],
                    reason:[
                        { required: true, message: '请至少选择一个', trigger: 'blur' }
                    ],
                    note:[
                        {}
                    ]
                },
                details:{
                    total:400,
                    locked:4,
                },
                //MOCK用户信息
                // userInfo: JSON.parse(sessionStorage.getItem('userInfo'))
                //store里的user信息
                userInfo: JSON.parse(this.$store.state.user)
            }
        },
        mounted(){
            // this.getUserInfo();
            //当前时间
            setInterval( ()=>{
                this.setNowTimes();
            },1000)
        },
        methods:{
            roomId(index){
                return "num"+index;
            },
            //时间
            setNowTimes(){
                let myDate = new Date();
                let month = myDate.getMonth()+1;
                this.nowTimes.yyyy = myDate.getFullYear();
                this.nowTimes.mm = String(month<10?'0'+ month: month);
                this.nowTimes.dd = String(myDate.getDate()<10?'0'+ myDate.getDate(): myDate.getDate()) ;
                this.nowTimes.hou = String(myDate.getHours()<10?"0"+myDate.getHours():myDate.getHours());
                this.nowTimes.min = String(myDate.getMinutes()<10?'0'+myDate.getMinutes():myDate.getMinutes());
                this.nowTimes.sec = String(myDate.getSeconds()<10?'0'+myDate.getSeconds():myDate.getSeconds());
            },
            //增加房间
            addRoom() {
                var roomNumber = prompt('新增房间号', '请输入新增房间号', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    // inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
                    // inputErrorMessage: '邮箱格式不正确'
                });
                // var roomNumber = prompt(this.prompt);
                console.log(roomNumber);
                // createRoom(roomNumber);
                this.roomList.push({room_number: roomNumber, isBusy: false});
                this.timeList.push({room_number: roomNumber});
            },
            deleteRoom(i){
                this.roomList.splice(i,1);
                this.timeList.splice(i,1);
            },

            //长期申请
            longApplicant(num) {
                this.$alert('长期申请' + num + '办公区域？', '提醒', {
                    confirmButtonText: '确定申请',
                    callback: action => {
                        this.$message({
                            type: 'info',
                            message: `action: ${ action }`
                        });
                    }
                });
            }
        }
    }
</script>

