<template>
    <div id="signIn">
        <div style="padding-left: 10px; border-left: 5px solid rgb(91,155,213);">业主登录</div>

        <el-row style="margin:20px 40px 0 40px;">
            <el-row>
                <p class="date">当前时间：{{nowTimes.yyyy}}-{{nowTimes.mm}}-{{nowTimes.dd}} {{nowTimes.hou}}:{{nowTimes.min}}:{{nowTimes.sec}}</p>
            </el-row>
            <el-row style="margin-top:20px;width: 30%; border-top: 2px solid rgb(91,155,213);background-color: white;padding: 20px 20px 0 0;line-height: 30px
">
                <el-form :model="signInForm" :rules="rules" ref="signInForm" label-width="125px" class="demo-signInForm">
                    <el-form-item
                            prop="email"
                            label="电子邮箱">
                        <el-input v-model="signInForm.email"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="pass">
                        <el-input type="password" v-model="signInForm.pass" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="submitForm('signInForm')">确定</el-button>
                    </el-form-item>
                </el-form>

            </el-row>
        </el-row>
    </div>

</template>

<script>
    export default {
        name: "CheckInOut",
        data(){
            return{
                nowTimes:{
                    yyyy:"0000",
                    mm:0,
                    dd:"00",
                    hou:"00",
                    min:"00",
                    sec:"00"
                },
                signInForm: {
                    pass: '',
                    email: '',
                },
                rules: {
                    pass: [
                        { required: true, message: '请输入密码', validator: validatePass, trigger: 'blur' }
                    ],
                    email: [
                        { required: true, message: '请输入邮箱地址', trigger: 'blur' },
                        { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
                    ]
                },
                //MOCK用户信息
                userInfo: JSON.parse(sessionStorage.getItem('userInfo'))
            };
            //登录
            var validatePass = (rule, value, callback) => {
                if (this.ruleForm.checkPass !== '') {
                    this.$refs.ruleForm.validateField('checkPass');
                }
                callback();
            };

        },
        mounted(){
            this.getUserInfo();
            setInterval( ()=>{
                this.setNowTimes();
            },1000)
        },
        methods:{
            //当前时间
            setNowTimes(){
                let myDate = new Date();
                this.nowTimes.yyyy = myDate.getFullYear();
                let month = myDate.getMonth()+1;
                this.nowTimes.mm = String(month<10?'0'+ month: month);
                this.nowTimes.dd = String(myDate.getDate()<10?'0'+ myDate.getDate(): myDate.getDate()) ;
                this.nowTimes.hou = String(myDate.getHours()<10?"0"+myDate.getHours():myDate.getHours());
                this.nowTimes.min = String(myDate.getMinutes()<10?'0'+myDate.getMinutes():myDate.getMinutes());
                this.nowTimes.sec = String(myDate.getSeconds()<10?'0'+myDate.getSeconds():myDate.getSeconds());
            },
            //登录
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('submit!');
                        this.$router.push('/checkinorout');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
        },


    }
</script>

