<template>
    <div id="email">
        <div style="padding-left: 10px; border-left: 5px solid #5B9BD5;">我的邮箱</div>
        <el-row style="margin:20px 40px 0 40px;">
            <el-col :span="12">
                <el-table id="table-email" style="margin-top:10px;border-top: 2px solid rgb(91,155,213);"
                          :data="tableData_email"
                          :current-page="currentPage">
                    <el-table-column width="40" prop type="index" :index="indexMethod">
                        <template slot-scope="scope">
                            <img src="../../assets/mail.png" width="15" v-if="!isRead">
                            <img src="../../assets/mail_read.png" width="15" v-if="isRead">
                        </template>
                    </el-table-column>
                    <el-table-column prop="theme" label="主题">
                        <template slot-scope="scope">
                            <div class="email_theme" @click="selectEmail">{{scope.row.theme}}</div>
<!--                            <router-link to="/email_detail" style="text-decoration: transparent;color: #606266;">{{scope.row.theme}}</router-link>-->
                        </template>
                    </el-table-column>
                    <el-table-column label="时间" prop="date" width="120">
                    </el-table-column>
                </el-table>
                <el-row class="pagination" style="margin-top: 10px;">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-size="PageSize"
                            layout="total, prev, pager, next, jumper"
                            :total="total">
                    </el-pagination>
                </el-row>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import router from "../../router";

    var listJson = {
        tableData_email:[{
            theme: '停车场占用',
            date: '2020-07-22'
        }, {
            theme: '健身房占用',
            date: '2020-07-22'
        }, {
            theme: '公共办公区占用',
            date: '2020-07-22'
        },{
            theme: '停车场占用',
            date: '2020-07-22'
        }, {
            theme: '健身房占用',
            date: '2020-07-22'
        }, {
            theme: '公共办公区占用',
            date: '2020-07-22'
        },{
            theme: '停车场占用',
            date: '2020-07-22'
        }, {
            theme: '健身房占用',
            date: '2020-07-22'
        }, {
            theme: '公共办公区占用',
            date: '2020-07-22'
        },{
            theme: '停车场占用',
            date: '2020-07-22'
        }, {
            theme: '健身房占用',
            date: '2020-07-22'
        }, {
            theme: '公共办公区占用',
            date: '2020-07-22'
        },{
            theme: '停车场占用',
            date: '2020-07-22'
        }, {
            theme: '健身房占用',
            date: '2020-07-22'
        }, {
            theme: '公共办公区占用',
            date: '2020-07-22'
        },{
            theme: '停车场占用',
            date: '2020-07-22'
        }, {
            theme: '健身房占用',
            date: '2020-07-22'
        },],
    }
    export default {
        name: "Email",
        data(){
            return{
                //总数据
                tableData_email:[],
                data:[],
                //默认显示第1页
                currentPage:1,
                //默认每页显示条数
                PageSize:12,
                //总条数
                total: null,
                //邮件已读图标
                isRead: false,
                //MOCK用户信息
                userInfo: JSON.parse(sessionStorage.getItem('userInfo'))
            }
        },
        mounted() {
            this.getUserInfo();
        },
        methods:{
            pageList(){
                // 发请求拿到数据并暂存全部数据,方便之后操作
                this.data = listJson.tableData_email;
                this.getList();
            },
            // 处理数据
            getList() {
                // es6过滤得到满足搜索条件的展示数据list
                let tableData_email = this.data.filter((item, index) =>
                    item.theme
                );
                //过滤分页
                this.tableData_email = tableData_email.filter((item, index) =>
                    index < this.currentPage * this.PageSize && index >= this.PageSize * (this.currentPage - 1)
                );
                //分页的总数据
                this.total = tableData_email.length;
            },
            indexMethod(index){
                return (index+1)+(this.currentPage-1)*this.PageSize;
            },
            // getData() {
            // console.log('生命周期')
            // this.axios.get('../../static/database.json').then(response => {
            // console.log(response.data);
            // this.tableData_email=response.data
            // }, response => {
            // console.log("error");
            // });
            // },
            // 分页
            // 每页显示的条数
            handleSizeChange(val) {
                // 改变每页显示的条数
                this.PageSize=val;
                this.getList();
            },
            // 显示第几页
            handleCurrentChange(val) {
                // 改变默认的页数
                this.currentPage=val;
                this.getList();
            },
            search(){
                this.currentPage=1;// 注意：在改变每页显示的条数时，要将页码显示到第一页
                this.getList();
            },
            //点击到详情页面
            selectEmail(){
                router.push({
                    path:'/email_detail',
                    query:{emailId:this.data.index}
                })
            }
        },
        created(){
            // this.getData()
            this.pageList();
        },
    }
</script>

<style>
#email .email_theme{
    cursor: pointer;
}
</style>