              {"createTime": "2018-8-7","resourceId":103,"approvalId": 1},{"createTime":"2018-8-8","resourceId":101,"approvalId": 1},{"createTime":"2018-8-9","resourceId":104,"approvalId": 1},
              {"createTime": "2018-8-7","resourceId":104,"approvalId": 1},{"createTime":"2018-8-8","resourceId":101,"approvalId": 1},{"createTime":"2018-8-9","resourceId":104,"approvalId": 1},
              {"createTime": "2018-8-7","resourceId":105,"approvalId": 1},{"createTime":"2018-8-8","resourceId":204,"approvalId": 1},{"createTime":"2018-8-9","resourceId":104,"approvalId": 1},
              {"createTime": "2018-8-7","resourceId":106,"approvalId": 1},{"createTime":"2018-8-8","resourceId":203,"approvalId": 1},{"createTime":"2018-8-9","resourceId":104,"approvalId": 1},
              {"createTime": "2018-8-7","resourceId":201,"approvalId": 1},{"createTime":"2018-8-8","resourceId":202,"approvalId": 1},{"createTime":"2018-8-9","resourceId":104,"approvalId": 1},];
          console.log((this.tableData_approval).length);
          //我的审批每页数量
          this.approval_PageSize = this.userInfo.roleId<=6?10:4;
          //我的审批列表 接口
          this.$axios.post('/myapprovaltotal',{
              loginId: Number(this.userInfo.userId),
              page: this.approval_PageSize,
              volumn: this.approval_currentPage,
          }).then((response)=>{
              if(response.status === 200){
                  // this.tableData_approval = response.data;
                  this.approval_data = this.tableData_approval;
                  console.log(this.tableData_approval);
                  this.getList_approval();
              }
          })
          .catch(function (error) {
              console.log(error)
          });

      },
      // 处理数据
      getList_todo() {
          console.log('getList_todo')
          // let tableData_todo = this.todo_data.filter((item, index) =>
          //     item.index
          // );
          let tableData_todo = this.todo_data;

          console.log('tableData_todo');
          console.log(tableData_todo.length)
          //过滤分页
          this.tableData_todo = tableData_todo.filter((item, index) =>
              index < this.todo_currentPage * this.todo_PageSize && index >= this.todo_PageSize * (this.todo_currentPage - 1)
          );
          //分页的总数据
          this.todo_total = tableData_todo.length;
      },

      getList_application() {
          // 我的申请
          // let tableData_application = this.application_data.filter((item, index) =>
          //     item.index
          // );
          let tableData_application = this.application_data;
          //过滤分页
          this.tableData_application = tableData_application.filter((item, index) =>
              index < this.application_currentPage * this.application_PageSize && index >= this.application_PageSize * (this.application_currentPage - 1)
          );
          //分页的总数据
          this.application_total = tableData_application.length;
      },
      getList_approval() {
          // 我的审批
          // let tableData_approval = this.approval_data.filter((item, index) =>
          //     item.index
          // );
          let tableData_approval = this.approval_data;
          //过滤分页
          this.tableData_approval = tableData_approval.filter((item, index) =>
              index < this.approval_currentPage * this.approval_PageSize && index >= this.approval_PageSize * (this.approval_currentPage - 1)
          );
          //分页的总数据
          this.approval_total = tableData_approval.length;
      },
      //待办函数
      todo_indexMethod(index){
          return (index+1)+(this.todo_currentPage-1)*this.todo_PageSize;
      },
      // 每页显示的条数
      todo_handleSizeChange(val) {
          // 改变每页显示的条数
          this.todo_PageSize=val;
          this.getList_todo();
      },
      // 显示第几页
      todo_handleCurrentChange(val) {
          // 改变默认的页数
          this.todo_currentPage=val;
          this.getList_todo();
      },
      //我的申请
      // 每页显示的条数
      application_handleSizeChange(val) {
          // 改变每页显示的条数
          this.application_PageSize=val;
          this.getList_application();
      },
      // 显示第几页
      application_handleCurrentChange(val) {
          // 改变默认的页数
          this.application_currentPage=val;
          this.getList_application();
      },

      //我的审批
      // 每页显示的条数
      approval_handleSizeChange(val) {
          // 改变每页显示的条数
          this.approval_PageSize=val;
          this.getList_approval();
      },
      // 显示第几页
      approval_handleCurrentChange(val) {
          // 改变默认的页数
          this.approval_currentPage=val;
          this.getList_approval();
      },
  }
}
