<template>
    <div id="publish">
        <div style="padding-left: 10px; border-left: 5px solid #5B9BD5;">综合服务\信息发布</div>
        <el-row style="margin:20px 40px 0 40px;">
            <el-col :span="12">
                <div class="block" style="display: inline;font-size: 14px;">
                    <span class="date">开始日期：</span>
                    <el-date-picker style="width: 150px;"
                                    v-model="startTime"
                                    type="date"
                                    placeholder="选择日期">
                    </el-date-picker>
                </div>
                <div class="block" style="display: inline;margin-left: 20px;font-size: 14px;">
                    <span class="date">结束日期：</span>
                    <el-date-picker style="width: 150px;"
                                    v-model="endTime"
                                    type="date"
                                    placeholder="选择日期">
                    </el-date-picker>
                </div>
                <div class="searchWord" style="font-size: 14px;margin-top: 10px;">
                    <div style="display: inline-block;">
                        主题：
                        <el-input v-model="searchData" style="margin-right:20px;width:150px;display: inline-block;"
                                  placeholder="请输入搜索内容">
                        </el-input>
                        <el-button type="primary" @click="search" size="small">搜索</el-button>
                    </div>
                </div>
                <el-table id="table-publish" style="margin-top:10px;border-top: 2px solid rgb(91,155,213);"
                          :data="tableData_publish"
                          :current-page="currentPage">
                    <el-table-column width="50" prop type="index" :index="indexMethod">
                    </el-table-column>
                    <el-table-column prop="theme" label="公告">
                        <template slot-scope="scope">
                            <router-link to="/publish_detail" style="text-decoration: transparent;color: #606266;">{{scope.row.theme}}</router-link>
                        </template>
                    </el-table-column>
                    <el-table-column label="时间" prop="date" width="120">
                    </el-table-column>
                </el-table>
                <el-row class="pagination" style="margin-top: 10px;">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-size="PageSize"
                            layout="total, prev, pager, next, jumper"
                            :total="total">
                    </el-pagination>
                    <el-button type="primary" @click="goToPublishApplication" style="float: right;" v-if="this.userInfo.roleId>6">我要申请</el-button>
                </el-row>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    var listJson = {
        tableData_publish:[{
            theme: '停车场占用',
            date: '2020-07-22'
        }, {
            theme: '健身房占用',
            date: '2020-07-22'
        }, {
            theme: '公共办公区占用',
            date: '2020-07-22'
        },{
            theme: '停车场占用',
            date: '2020-07-22'
        }, {
            theme: '健身房占用',
            date: '2020-07-22'
        }, {
            theme: '公共办公区占用',
            date: '2020-07-22'
        },{
            theme: '停车场占用',
            date: '2020-07-22'
        }, {
            theme: '健身房占用',
            date: '2020-07-22'
        }, {
            theme: '公共办公区占用',
            date: '2020-07-22'
        },{
            theme: '停车场占用',
            date: '2020-07-22'
        }, {
            theme: '健身房占用',
            date: '2020-07-22'
        }, {
            theme: '公共办公区占用',
            date: '2020-07-22'
        },{
            theme: '停车场占用',
            date: '2020-07-22'
        }, {
            theme: '健身房占用',
            date: '2020-07-22'
        }, {
            theme: '公共办公区占用',
            date: '2020-07-22'
        },{
            theme: '停车场占用',
            date: '2020-07-22'
        }, {
            theme: '健身房占用',
            date: '2020-07-22'
        },],
    }
    export default {
        name: "Publish",
        data(){
            return{
                //是物业？
                isProperty: false,
                //总数据
                tableData_publish:[],
                data:[],
                //默认显示第1页
                currentPage:1,
                //默认每页显示条数
                PageSize:6,
                //总条数
                total: null,
                //搜索
                searchData: '',
                //时间段
                startTime:'',
                endTime:'',
                //MOCK用户信息
                // userInfo: JSON.parse(sessionStorage.getItem('userInfo'))
                //store里的user信息
                userInfo: JSON.parse(this.$store.state.user)
            }

        },
        mounted() {
            this.getUserInfo();
        },
        methods:{
            pageList(){
                // 发请求拿到数据并暂存全部数据,方便之后操作
                this.data = listJson.tableData_publish;
                this.getList();
            },
            // 处理数据
            getList() {
                // es6过滤得到满足搜索条件的展示数据list
                let tableData_publish = this.data.filter((item, index) =>
                    item.theme.includes(this.searchData)
                );
                //过滤分页
                this.tableData_publish = tableData_publish.filter((item, index) =>
                    index < this.currentPage * this.PageSize && index >= this.PageSize * (this.currentPage - 1)
                );
                //分页的总数据
                this.total = tableData_publish.length;
            },
            indexMethod(index){
                return (index+1)+(this.currentPage-1)*this.PageSize;
            },
            // getData() {
                // console.log('生命周期')
                // this.axios.get('../../static/database.json').then(response => {
                // console.log(response.data);
                // this.tableData_publish=response.data
                // }, response => {
                // console.log("error");
                // });
            // },
            // 分页
            // 每页显示的条数
            handleSizeChange(val) {
                // 改变每页显示的条数
                this.PageSize=val;
                this.getList();
            },
            // 显示第几页
            handleCurrentChange(val) {
                // 改变默认的页数
                this.currentPage=val;
                this.getList();
            },
            goToPublishApplication(){
                this.$router.replace("/publish_application")
            },
            search(){
                this.currentPage=1;// 注意：在改变每页显示的条数时，要将页码显示到第一页
                this.getList();
            },
        },
        created(){
            // this.getData()
            this.pageList();
        },
    }
</script>

