<template>
    <div id="changepassword">
        <div style="padding-left: 10px; border-left: 5px solid rgb(91,155,213);">
            <router-link to="/Center" style="color: #606266;">个人中心</router-link>
            \ 修改密码
        </div>

        <el-row style="margin-top:20px;margin-left: 40px;width: 30%; border-top: 2px solid rgb(91,155,213);background-color: white;padding: 20px 20px 0 0;line-height: 30px
">
            <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="原始密码" prop="pass0">
                    <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
<!--                    <span><router-link to="/retrivepassword" style="color: #dddddd;line-height: 14px;">忘记密码？</router-link></span>-->
                </el-form-item>
                <el-form-item label="密码" prop="pass">
                    <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="确认密码" prop="checkPass">
                    <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')">确定</el-button>
                </el-form-item>
            </el-form>

        </el-row>
    </div>

</template>

<script>
    export default {
        name: "ChangePassword",
        data() {
            var validatePass0 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入原始密码'));
                } else {
                    // if (this.ruleForm.checkPass !== '') {
                    //     this.$refs.ruleForm.validateField('checkPass');
                    // }
                    // callback();
                }
            };
            var validatePass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入密码'));
                } else {
                    if (this.ruleForm.checkPass !== '') {
                        this.$refs.ruleForm.validateField('checkPass');
                    }
                    callback();
                }
            };
            var validatePass2 = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请再次输入密码'));
                } else if (value !== this.ruleForm.pass) {
                    callback(new Error('两次输入密码不一致!'));
                } else {
                    callback();
                }
            };
            return {
                ruleForm: {
                    pass0: '',
                    pass: '',
                    checkPass: '',
                },
                rules: {
                    pass0:[
                        { required: true, validator: validatePass0, trigger: 'blur' }
                    ],
                    pass: [
                        { required: true, validator: validatePass, trigger: 'blur' }
                    ],
                    checkPass: [
                        { required: true, validator: validatePass2, trigger: 'blur' }
                    ],
                },
                //MOCK用户信息
                userInfo: JSON.parse(sessionStorage.getItem('userInfo'))
            };
        },
        mounted() {
            this.getUserInfo();
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
        }
    }
</script>

<style>
#changepassword .el-form-item__content{
    line-height: 16px;
}
</style>