<template>
    <div id="publish_application">
        <div style="padding-left: 10px; border-left: 5px solid #5B9BD5;">综合服务\信息发布</div>
        <el-row style="margin-top:20px;margin-left: 40px;width: 60%; border-top: 2px solid rgb(91,155,213);background-color: white;padding: 20px 20px 0 0;line-height: 30px
">
            <el-form :model="publishForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="主题" prop="theme">
                    <el-input v-model="publishForm.theme"></el-input>
                </el-form-item>
                <el-form-item label="内容" prop="content">
                    <el-input type="textarea"
                              placeholder="请输入内容" :rows="10" v-model="publishForm.content">
                    </el-input>
                </el-form-item>
                <el-form-item>
                    <el-button style="float: right;" type="primary" @click="submitForm('publishForm')">确定</el-button>
                </el-form-item>
            </el-form>

        </el-row>
    </div>
</template>

<script>
    export default {
        name: "Publish_application",
        data() {
            return {
                publishForm: {
                    theme: '',
                    content: '',
                },
                rules: {
                    theme:[
                        { required: true, message: "请输入主题" , trigger: 'blur' }
                    ],
                    content: [
                        { required: true, message: "请输入内容", trigger: 'blur' }
                    ],
                },
                //MOCK用户信息
                userInfo: JSON.parse(sessionStorage.getItem('userInfo'))
            };
        },
        mounted() {
            this.getUserInfo();
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
        }
    }
</script>

