<template>
    <div id="retrivepassword">
        <el-row style="height: 80px;background-color: rgb(255,255,255,.9);">
<!--            <img src="../../assets/logo.png" style="position: absolute;bottom: 10px;left: 100px;">-->
            <span style="position: absolute;bottom: 10px;left: 450px;font-size: 24px;">园区共享服务大数据支持平台</span>
        </el-row>
        <div class="login-container">
            <el-form :model="retriveForm" :rules="retriveRules" ref="retriveForm" label-width="80px" class="login-form">
                <el-form-item prop="email" label="电子邮箱">
                    <el-input v-model="retriveForm.email"></el-input>
                </el-form-item>
                <el-form-item label="密保问题" prop="question">
                    <el-select v-model="retriveForm.question">
                        <el-option label="您的生日？" value="question1"></el-option>
                        <el-option label="毕业于哪个初中？" value="question2"></el-option>
                        <el-option label="您最喜欢的电影？" value="question3"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="答案" prop="answer">
                    <el-input v-model="retriveForm.answer"></el-input>
                </el-form-item>
                <el-form-item label="新密码" prop="pass">
                    <el-input type="password" v-model="retriveForm.pass" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('retriveForm')">确定</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
    export default {
        name: "RetrivePassword",
        data() {
            var validatePass = (rule, value, callback) => {

            };
            return {
                retriveForm: {
                    pass: '',
                    email: '',
                    question:'',
                    answer: '',
                },
                retriveRules: {
                    pass: [
                        { required: true, message: '请输入新密码', trigger: 'blur' }
                    ],
                    email: [
                        { required: true, message: '请输入邮箱地址', trigger: 'blur' },
                        { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
                    ],
                    question: [
                        { required: true, message: '请选择一个密保问题', trigger: 'blur', type:'number'},
                    ],
                    answer: [
                        { required: true, message: '请输入答案', trigger: 'blur' },
                    ]
                },
                //MOCK用户信息
                // userInfo: JSON.parse(sessionStorage.getItem('userInfo'))
            };
        },
        mounted() {
            // this.getUserInfo();
        },
        methods: {
            submitForm(formName) {
                this.$refs.retriveForm.validate((valid) => {
                    if (valid) {
                        this.$axios.post('/password/change',{
                            question_id: this.retriveForm.question,
                            email: this.retriveForm.email,
                            answer: this.retriveForm.answer,
                            pass: this.retriveForm.pass
                        })
                        .then((response)=>{
                            if (response.status === 200){
                                alert('submit!');
                            }
                        })
                        .catch(function (error) {
                            console.log(error)
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
        }
    }
</script>

<style>
    #retrivepassword{
        width: 100vw;
        height: 100vh;
        background-image: url("../../assets/bg.jpg");
    }
</style>