<!--                            选中-->
<!--                        </div>-->
<!--                    </div>-->
                </el-row>
                <!--选择时段-->
                <el-row style="margin-top: 20px;">
                    <span class="date">选择时段：</span>
                    <div class="block" style="display: inline-block">
                        <el-form :model="applyForm" :rules="rules" ref="applyForm">
                            <el-time-select
                                    v-model="applyForm.startTime"
                                    format="yyyy-MM-dd HH:mm:ss"
                                    value-format="yyyy-MM-dd HH:mm:ss"
                                    :picker-options="{
                                    start: startHour,
                                    step: '00:60',
                                    end: '24:00'
                                  }">
                            </el-time-select>
                            -
                            <el-time-select
                                    v-model="applyForm.endTime"
                                    :picker-options="{
                                    start: this.applyForm.startTime,
                                    step: '00:60',
                                    end: '24:00'
                                  }">
                            </el-time-select>
                        </el-form>

<!--                        <span>0:00</span><span style="margin-left: 95px">4:00</span>-->
<!--                        <span style="margin-left: 95px">8:00</span><span style="margin-left: 90px">12:00</span>-->
<!--                        <span style="margin-left: 85px">16:00</span><span style="margin-left: 85px">20:00</span>-->
<!--                        <span style="margin-left: 85px">24:00</span>-->
