<!--                            <div class="hour_block" id="hour22"></div>-->
<!--                            <div class="hour_block" id="hour23"></div>-->
<!--                        </el-row>-->
                    </div>
                </el-row>
            </div>
            <!--申请详情-->
            <el-row v-if="this.userInfo.roleId>=7" class="owner" style="margin-top: 20px;">
                <el-col class="quantity" :span="4">
                    <div class="quantity-content">
                        <span style="color: #DEEBF7;font-size: 50px;">280</span><br>
                        <span style="font-size: 12px">空闲车位</span>
                        <el-button type="primary" @click="dialogFormVisible = true" style="margin-top: 20px;">我要申请</el-button>
                        <el-dialog :visible.sync="dialogFormVisible">
                            <el-form :model="applyForm" :rules="rules" ref="applyForm" label-width="100px" class="demo-dynamic">
                                <el-form-item label="主题" prop="theme" style="width:500px;">
                                    <el-input v-model="applyForm.theme"></el-input>
                                </el-form-item>
                                <el-form-item label="数量" prop="number" style="width:500px;">
                                    <el-input v-model="applyForm.number"></el-input>
                                </el-form-item>
                                <el-form-item label="申请原因" prop="reason" style="width:300px;">
                                    <el-select v-model="applyForm.reason">
                                        <el-option label="例会" value="例会"></el-option>
                                        <el-option label="团建活动" value="团建活动"></el-option>
                                        <el-option label="商业会谈" value="商业会谈"></el-option>
                                        <el-option label="培训" value="培训"></el-option>
                                        <el-option label="其它" value="其它"></el-option>
                                    </el-select>
                                </el-form-item>
                                <el-form-item label="备注" prop="note" style="width:500px;">
                                    <el-input v-model="applyForm.note"></el-input>
                                </el-form-item>
                            </el-form>
                            <div slot="footer" class="dialog-footer">
                                <el-button @click="dialogFormVisible = false">取 消</el-button>
                                <el-button type="primary" @click="submitApplication">确 定</el-button>
