</template>

<script>
    export default {
        name: "ParkingLot",
        data(){
            return{
                //物业界面
                parkDetails:{
                    rest:'',
                    applied:'',
                    in:'',
                    locked:'',
                    total:'',
                },
                //时间
                nowTimes:{
                    yyyy:"0000",
                    mm:0,
                    dd:"00",
                    hou:"00",
                    min:"00",
                    sec:"00"
                },
                //物业选择未来时间段查看停车场信息
                selectTime:{
                    startTime:'',
                    endTime:'',
                    selectDate:'',
                },
                // 查看设置今天及之后的日期
                pickerOptions1: {
                    disabledDate(time) {
                        return time.getTime() < Date.now()-8.64e7;//如果没有后面的-8.64e7就是不可以选择今天的
                    }
                },
                // 申请设置只能选择明天及之后的日期
                pickerOptions0: {
                    disabledDate(time) {
                        return time.getTime() < Date.now();//如果没有后面的-8.64e7就是不可以选择今天的
                    }
                },
                //申请开始时间
                startHour:'',
                //弹出申请详情
                dialogFormVisible: false,
                applyForm: {
                    theme: '', //主题
                    number: '',//申请数量
                    note: '',//备注
                    reason: '', //申请原因
                    date: '',//申请日期
                    startTime:'',//开始时间
                    endTime:'',//结束时间
                },
                rules:{
                    date:[
                        { required: true, message: '请选择日期', trigger: 'blur' },
                    ],
                    startTime:[
                        { required: true, message: '请选择日期', trigger: 'blur' },
                    ],
                    endTime:[
                        { required: true, message: '请选择日期', trigger: 'blur' },
                    ],
                    theme:[
                        { required: true, message: '请输入主题', trigger: 'blur' },
                    ],
                    number:[
                        { required: true, message: '请填写申请数量', trigger: 'change' }
                    ],
                    reason:[
                        { required: true, message: '请至少选择一个', trigger: 'blur' }
                    ],
                    note:[
                        {}
                    ]
                },
                //停车场信息
                details:{
                    total: 400,
                    locked: 4,
                },
                //MOCK用户信息
                // userInfo: JSON.parse(sessionStorage.getItem('userInfo'))
                //store里的user信息
                userInfo: JSON.parse(this.$store.state.user)
            }
        },
        mounted(){
            //当前时间
            setInterval( ()=>{
                this.setNowTimes();
            },1000)

            this.getUserInfo();
        },
        methods:{
            //时间
            setNowTimes(){
                let myDate = new Date();
                let month = myDate.getMonth()+1;
                let s_hour = myDate.getHours()+1;
                this.nowTimes.yyyy = myDate.getFullYear();
                this.nowTimes.mm = String(month<10?'0'+ month: month);
                this.nowTimes.dd = String(myDate.getDate()<10?'0'+ myDate.getDate(): myDate.getDate()) ;
                this.nowTimes.hou = String(myDate.getHours()<10?"0"+myDate.getHours():myDate.getHours());
                this.nowTimes.min = String(myDate.getMinutes()<10?'0'+myDate.getMinutes():myDate.getMinutes());
                this.nowTimes.sec = String(myDate.getSeconds()<10?'0'+myDate.getSeconds():myDate.getSeconds());
                this.startHour = String(s_hour<10?("0"+s_hour+':00'):(s_hour+':00'));
                },
            //签到
            goToLink(){
                this.$router.push('/sign')
            },
            //物业查询时间段
            searchTime(){
                this.$refs.selectTime.validate(valid => {
                    if(valid){
                        this.$axios.post('Physical/Park/Selectinfo',{
                            starttime: JSON.stringify(this.selectTime.selectDate) + JSON.stringify(this.selectTime.startTime),
                            endtime:  JSON.stringify(this.selectTime.selectDate) +  JSON.stringify(this.selectTime.endTime),
                        })
                        .then((response)=> {
                            let res = response.data;
                            // this.parkDetails.rest = response.data.rest,
                            // this.parkDetails.applied = response.data.taken;
                            // if(response.status==200){
                            this.parkDetails.applied = res.taken;
                            this.parkDetails.total = res.total;
                            this.parkDetails.locked = res.reserve;
                            this.parkDetails.rest = this.parkDetails.total-this.parkDetails.applied-this.parkDetails.locked
                                // this.$set(this.parkDetails.applied,0,res.taken);
                                // this.$set(this.parkDetails.total,0,res.total);
                                // this.$set(this.parkDetails.locked,0,res.reserve);
                                // var rest = this.parkDetails.total-this.parkDetails.applied-this.parkDetails.locked;
                                // this.$set(this.parkDetails.rest,0,rest);
                            // }
                            // reserve:,
                            // locked:,
                        })
                        .catch(function (error) {
                            console.log(error)
                        })
                    }
                })
            },
            //提交申请
            submitApplication(){
                this.$refs.applyForm.validate(valid => {
                    if (valid) {
                        this.$axios.post('/Physical/Park/urgapply', {
                            reason: JSON.stringify(this.applyForm.reason),
                            num: JSON.stringify(this.applyForm.number),
                            note: JSON.stringify(this.applyForm.note),
                            theme: JSON.stringify(this.applyForm.theme),
                            starttime: JSON.stringify(this.applyForm.date)+JSON.stringify(this.applyForm.startTime),
                            endtime: JSON.stringify(this.applyForm.date)+JSON.stringify(this.applyForm.endTime),
                            userid: JSON.stringify(this.userInfo.userId)
                            })
                            .then((response) => {
                                if (response.status === 200) {
                                    console.log(JSON.stringify(this.applyForm.reason))

                                    this.$message({
                                        message: '登录成功',
                                        type: 'success'
                                    })
                                }
                            })
                            .catch(function (error) {
                                console.log(error)
                            })
                    } else {
                        console.log('error submit!!')
                        return false
                    }
                })
            }
        }
    }
</script>

<style>
    div[class^="hour_block"]{
        width: 30px;height: 30px;display: inline-block;background-color: #DEEBF7;margin-left: 2px;}
    #parkinglot .el-dialog{
        width: 600px;
    }
</style>