<template>
    <div id="login">
        <el-row style="height: 80px;background-color: rgb(255,255,255,.9);">
<!--            <img src="../../assets/logo.png" style="position: absolute;bottom: 10px;left: 100px;">-->
            <span style="position: absolute;bottom: 10px;left: 450px;font-size: 24px;">园区共享服务大数据支持平台</span>
        </el-row>
        <div class="login-container">
            <el-form ref="loginForm" :rules="rules" :model="loginForm" :label-position="labelPosition" label-width="80px" class="login-form">
                <el-form-item label="用户名" prop="username">
                    <el-input v-model="loginForm.username"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input type="password" v-model="loginForm.password"></el-input>
                    <el-button type="text" @click="goToRetrivePass" style="text-decoration: underline;position: absolute;right: 0;bottom: -30px;">忘记密码</el-button>
                </el-form-item>

                <el-button type="primary" @click="submitLogin(loginForm)" style="margin-left:0;width: 350px">登录</el-button>
                <el-button type="normal" @click="goToRegister" style="display: block;width: 350px;margin-left: 0;margin-top: 15px">注册</el-button>
            </el-form>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Login",
        data() {
            var validateName=(rule, value, callback)=>{
                if (value === '') {
                    callback(new Error('请输入用户名'));
                }
                callback();
            };
            var validatePass=(rule, value, callback)=>{
                if (value === '') {
                    callback(new Error('请输入密码'));
                }
                    callback();
            };
            return {
                loginForm: {
                    username: "parking@163.com",
                    password: "123456"
                },
                labelPosition:'left',
                rules:{
                    username:[
                        {required: true, validator: validateName, trigger: 'blur'},
                    ],
                    password: [
                        {required: true, validator: validatePass, trigger: 'blur'},
                    ]
                },
                //MOCK用户信息
                // userInfo: [],
            };
        },
        methods: {
            goToRetrivePass(){
                this.$router.push("/retrivepassword")
            },
            //提交登录表单
            submitLogin () {
                this.$refs.loginForm.validate(valid => {
                    if (valid) {
                        this.$axios.post('/login', {
                            user: this.loginForm.username,
                            pass: this.loginForm.password
                        })
                            .then((response) => {
                                // console.log(typeof (response.data))//object
                                let data = response.data;
                                // let data11 = {"token":"1233456","user":"{\"answer_1\":\"qw\",\"answer_2\":\"a2\",\"answer_3\":\"a3\",\"companyName\":\"开发中心\",\"depName\":\"研发部\",\"email\":\"12@qq.com\",\"lineLeader\":\"呵呵\",\"phoneNum\":\"123456\",\"roleId\":1,\"userId\":1,\"userName\":\"李诗颖\"}"};
                                // if (response.status === 200) {
                                if(data.success === true){
                                    console.log('login-response');
                                    // this.$store.commit('SET_TOKEN', data["Authentication-Token"]);
                                    console.log(typeof (data.data));//object
                                    console.log(data.data);
                                    // var dataObj = eval("("+data.data+")");
                                    // var userInfo = dataObj.userInfo;
                                    // var token = dataObj.token;

                                    // console.log(typeof (data11.user))//string
                                    // var data111 = JSON.parse(data11.user);//成功

                                    // console.log(typeof(data0));//string
                                    this.$store.commit('SET_TOKEN',data.data.token);
                                    console.log(typeof(data.data));//后端传string过来自动转object
                                    // console.log(typeof(userInfo));
                                    this.$store.commit('GET_USER', JSON.stringify(data.data.userInfo));
                                    console.log(this.$store.state.token)
                                    if(this.$store.state.token) {
                                        this.$message({
                                            message: data.message,
                                            type: 'success'
                                        });
                                        this.$router.push('/home');
                                    }
                                }else {
                                    if(data.state === "0001"){
                                        this.$message({
                                            message: "用户名错误",
                                            type: 'warning'
                                        });
                                    }else if(data.state === "0002"){
                                        this.$message({
                                            message: "密码错误",
                                            type: 'warning'
                                        });
                                    }else if(data.state === "0003"){
                                        this.$message({
                                            message: "用户不存在",
                                            type: 'warning'
                                        });
                                    }
                                }
                            })
                            .catch(function (error) {
                                console.log(error)
                            })
                    } else {
                        console.log('error submit!!')
                        return false
                    }
                })
            },
            goToRegister(){
                this.$router.push("/register")
            },
        },
        mounted() {
        }
    }
</script>

<style>
    #login{
        width: 100vw;
        height: 100vh;
        background: url("../../assets/bg.jpg");
    }
    .login-form {
        width: 350px;
        margin:200px auto; /* 上下间距160px，左右自动居中*/
        background-color: rgb(255,255,255,.7) ;/* 透明背景色 */
        padding: 30px;
        border-radius: 10px; /* 圆角 */
    }

    /* 背景 */
    .login-container {
        position: absolute;
        width: 100%;
    }

</style>